<template>
  <div class="result">
      <div>
        <textarea class="surface code-block"
            v-model="result"
            v-on:click="copy" spellcheck="false">
        </textarea>
        <div class="tag">
            Macro #{{macroIndex}}
        </div>
      </div>
    
  </div>
</template>

<script>

export default {
  name: 'result',
  components: {
    
  },
  data(){
      return{}
  },
  props:{
      result:String,
      macroIndex:Number
  },
  methods:{
      copy(e){
      e.path[0].select();
      document.execCommand("copy");
      this.$emit('show-toast',true);
    }
  },
}
</script>

<style lang="scss" scope>
.result{
    >div{
        display: inline-block;
    }
}
.tag{
    position: absolute;
    top: 20px;
    left: 20px;
    opacity: 0.56;
    font-size:14px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
