<template>
  <div class="skill"  v-on:click="addStep"
  v-bind:style="{backgroundImage: 'url('+ skill.image+')'}">
    <p class="cp">{{skill.cp}}</p>
    <div class="tip">
      <p>{{skillName}}({{skill.nameKO}})</p>
    </div>
  </div>
</template>

<script>
// import EventBus from '../store/EventBus.js'
import StepList from '../store/steps.js'
export default {
  name: 'skill',
  data(){
    return{
      stepList:StepList,
      skillName:this.skill.nameEN
    }
  },
  methods:{
    addStep(){
      // EventBus.$emit('step',this.skill);
      let addStepItem = {
        nameKO: this.skill.nameKO,
        nameEN: this.skill.nameEN,
        nameJP: this.skill.nameJP,
        cp:this.skill.cp,
        type:this.skill.type,
        level:this.skill.level,
        image:this.skill.image
      }
      this.stepList.push(addStepItem);
    }
  },
  props:{
    skill:Object,
    language:String
  },
  watch:{
    language(){
      if(this.language == 'en'){
        this.skillName = this.skill.nameEN
      }else if(this.language == 'jp'){
        this.skillName = this.skill.nameJP
      }else{
        this.skillName = this.skill.nameKO
      }
    }
  }
}
</script>

<style lang="scss" scope>
.skill{
  display: inline-block;
  width: 40px;
  height: 40px;
  vertical-align: top;
  margin-right:10px;
  cursor: pointer;
  background-repeat: no-repeat
}
.cp{
  font-size:12px;
  color:white;
  position: absolute;
  right:2px;
  bottom:0;
  text-shadow: 0 0 3px black;
}
.tip{
  display: none;
  background: rgba(255, 255, 255, 0.87);
  padding: 4px;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.87);
  position: absolute;
  white-space: nowrap;
  z-index: 1;
  top: -10px;
  left: 10px;
  margin: auto;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.skill:hover .tip{
  display: block;
}
</style>
