          <label>
            <p></p>
            <select v-model="language">
              <option value="en"></option>
              <option value="jp"></option>
              <option value="ko" disabled></option>
            </select>
          </label>
          <label>
            <p></p>
            <input type="number" min="0" max="30" v-model="activeWait">
            <p></p>
          </label>
          <label>
            <p>()</p>
            <input type="number" min="0" max="30" v-model="buffWait">
            <p></p>
          </label>
        </div>
      </div>
      <!-- // -->
      <!--  -->
      <div class="row list-step">
        <h2 class="title"></h2>
        <p class="caption">  CP : {{cp}}</p>
        <button class="btn-all-remove" v-on:click="removeAllStep"></button>
        <div class="surface">
          <p class="placeholder" v-if="placeholder">   </p>
          <div class="list">
            <transition-group name="stepitem" tag="div">
              <Step v-bind:step="stepList"
                v-for="(stepList,index) in stepList"
                v-bind:key="index">
              </Step>
            </transition-group>
          </div>
        </div>
      </div>
      <!-- // -->
      <!--  -->
