        <div>
          <h3 class="title"> </h3>
          <p class="caption">   .</p>
        </div>
        <div>
          <div class="surface code-block" v-if="placeholder"></div>
          <Result
          v-bind:result="result"
          v-bind:macroIndex="index+1"
          v-for="(result,index) in result"
          v-bind:key="index"
          v-on:show-toast="toast = $event"
          ></Result>
        </div>
      </div>
      <!-- // -->
      <!--  -->
      <transition name="toastAct">
        <div class="toast" v-if="this.toast" >
            !
        </div>
      </transition>
      <!-- // -->
      <!--  -->
      <transition name="notice">
        <div class="notice" v-if="this.notice">
          <p>-    .</p>
          <button class="close" v-on:click="removeNotice">!</button>
        </div>
      </transition>
      <!-- // -->
    </div>
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import Item from "./components/Item.vue";
import Step from "./components/Step.vue"
import Result from "./components/Result.vue"
import StepList from './store/steps.js'
import ResultList from './store/result.js'
import { mapGetters } from "vuex";

export default {
  name: "app",
  components: {
    Header,
    Item,
    Step,
    Result,
  },
  data(){
    return{
      stepList:StepList,
      cp:0,
      result:[],
      toast:false,
      notice:false,
      placeholder:true,
      activeWait:3,
      buffWait:2,
      language:'en'
    }
  },
  mounted(){
  },
  methods:{
    removeAllStep(){
      this.stepList.splice(0)
    },
    removeToast(){
      this.toast = false;
    },
    printMacro(){
      let totalCp = 0;
      let totalResult=[];
      let totalMacro=[];
      let resultItem;
      //Placeholder
      if(this.stepList.length != 0){
        this.placeholder = false;
      }else{
        this.placeholder = true;
      }
      
      //Array Reset
      for(var k=0;k<this.stepList.length;k++){
        let blockIndex = parseInt(k/15);
        totalResult[blockIndex]= '';
        totalMacro[blockIndex]='';
      }


      for(var i=0;i<this.stepList.length;i++){

        //get total cp
        if(this.stepList[i].cp != ''){
          totalCp += parseInt(this.stepList[i].cp);
        }

        //Language
        if(this.language == 'en'){
          resultItem = this.stepList[i].nameEN;
        }else if(this.language == 'jp'){
          resultItem = this.stepList[i].nameJP;
        }else{
          resultItem = this.stepList[i].nameKO;
        }

        //skill type check
        let blockIndex = parseInt(i/15);
        if(this.stepList[i].type == 'buff'){
          totalResult[blockIndex] += '/ac "'+resultItem+'" <me><wait.'+this.buffWait+'> \n'
        }else{
          totalResult[blockIndex] += '/ac "'+resultItem+'" <me><wait.'+this.activeWait+'> \n'
        }
      }
      this.cp = totalCp;
      this.result = totalResult;
    },
    removeNotice(){
      this.notice = false
    }
  },
  watch:{
    stepList(){
      this.printMacro();
    },
    activeWait(){
      this.printMacro();
    },
    buffWait(){
      this.printMacro();
    },
    language(){
      this.printMacro();
    },
    toast(){
      setTimeout(this.removeToast,1800)
    }
  },
  computed: {
    ...mapGetters({
      SKILL: "SKILL"
    })
  }
};


</script>

