<template>
  <div class="step" 
  v-bind:style="{backgroundImage: 'url('+ step.image+')'}"
  v-on:click="removeStep">
    <p class="cp">{{step.cp}}</p>
  </div>
</template>

<script>
import StepList from '../store/steps.js'
export default {
  name: "",
  components: {},
  data(){
    return{
      stepList:StepList,
    }
  },
  methods:{
    removeStep(){
      let index = this.stepList.indexOf(this.step);
      this.stepList.splice(index,1)
    }
  },
  props:{
    step: Object
  }
};
</script>

<style lang="scss" scope>
.step{
  display: inline-block;
  width: 40px;
  height: 40px;
  vertical-align: top;
  margin-right:10px;
  cursor: pointer;
  font-size:14px;
  color:black;
  background-repeat: no-repeat;
  font-size:0;
  position: relative;
  .cp{
    font-size:12px;
    color:white;
    position: absolute;
    right:2px;
    bottom:0;
    text-shadow: 0 0 3px black;
  }
  &:nth-child(15n){
    margin-right:120px;
  }
  &:nth-child(n+16){
    margin-top:10px;
  }
}
</style>

